      h1 Sociolingstica

    p.mb-4(data-aos="fade-in") La sociolingstica se centra en fronteras sociales, identifica los procesos de cambios en la lengua y la heterogeneidad lingstica y social y se orienta especialmente en los centros urbanos; la sociolingstica intenta dar respuesta a diferentes interrogantes, los cuales se conocen a continuacin:

