                h3 Segunda pregunta
                p.mb-0 Por qu suenan diferente, por ejemplo, un mexicano, un espaol y un chileno cuando hablan la misma lengua, el castellano o el espaol?
            .col-11.mb-4
              .tarjeta.crd-cyan.p-4
                h3 Tercera pregunta
                p.mb-0 Qu nos motiva a elegir una forma de decir algo entre varias opciones similares?
            .col-11.mb-4
              .tarjeta.crd-cyan.p-4
                h3 Cuarta pregunta
