    p.mb-5(data-aos="fade-in") La sociolingstica abarca una amplia variedad de tendencias o fenmenos en lo referente al estudio de una o ms lenguas en el contexto social, por lo que definirla de manera precisa es una tarea compleja. Dichos fenmenos se relacionan con los siguientes factores sociales:

