          .crd--avatarV--body
            p.bold.text-center Factores individuales de la sociedad en general (edad, raza, sexo, educacin).

      .col-lg-3.col-md-8.col-10.mb-4.mb-lg-0(data-aos="flip-right")
        .crd--avatarV.clr--blue
