              img(src="@/assets/curso/temas/t2/t2_05.svg", alt="alt").img85.m-auto
          .crd--avatarV--body
            p.bold.text-center Contexto externo en que ocurren los hechos lingsticos.

    .row.justify-content-center.mb-4(data-aos="zoom-in")
      .col-lg-10
        .bloque-texto-g.color-rosa.p-3.p-sm-4.p-md-5.mb-4
