          )
          .bloque-texto-g__texto.p-4
            p.mb-0 La sociolingstica tiene una relacin muy estrecha con la dialectologa, porque ambas son disciplinas que estudian la lengua hablada y el uso lingstico relacionado con ciertos grupos de individuos. Adems, las dos contemplan la heterogeneidad lingstica, lo que se evidencia en parmetros que se correlacionan con variaciones de estilo llamadas diferencias diafsicas.

    p.text-center.mb-5(data-aos="fade-in") Ahora, se invita a revisar con atencin los siguientes conceptos que permiten entender los diferentes enfoques respecto al estudio de la lengua.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.col-10.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t2/t2_07.svg", alt="alt")
      .col-lg-7(data-aos="fade-right")
         LineaTiempoD.color-secundario
          .row(numero="1" titulo="Diasistema")
            .col-12
              p.mb-0 La coexistencia de sistemas en toda la lengua, es un conjunto de dialectos, niveles y estilos de lenguaje.  No es un sistema lingstico, sino una serie de sistemas que se relacionan entre s formando un conjunto que no constituye una lengua unitaria, sino variable y en cambio constante.

          .row(numero="2" titulo="Diatopa")
            .col-12
              p.mb-0 Segn la Real Academia Espaola, este trmino hace referencia a una diferencia lingstica que se debe a las diferentes procedencias geogrficas de los hablantes.

          .row(numero="3" titulo="Diastrata")
            .col-12
              p.mb-0 Cambios de una lengua por razones socioculturales, este trmino hace referencia puntualmente al estudio de los diferentes niveles socioculturales de los hablantes.

          .row(numero="4" titulo="Diafasa")
            .col-12
              p.mb-0 Variable determinada por la situacin en la cual el hablante se encuentra (contexto, interlocutores, circunstancias o finalidad de la comunicacin) y segn el tipo de relacin entre los interlocutores, se selecciona el registro lingstico, sea formal o informal.

