            h4.mb-0 Dialecto

    p(data-aos="fade-in").mb-4 La definicin de dialecto en el folclor popular hace referencia a las formas rsticas o inferiores de hablar una lengua. Tambin se refiere a las lenguas minoritarias o con pocos hablantes, que no tienen el estatus oficial.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-in")
        p Por otro lado, en el uso tcnico de la palabra dialecto, para el lingista, es una variedad de lengua que comparte una comunidad. Por lo tanto, para algunos acadmicos el trmino dialecto puede tener connotaciones peyorativas, por lo que prefieren llamarlo de manera ms neutral: variedad de lengua. Para otros, es establecer criterios objetivos para determinar lo que es un dialecto y delimitarlo con suficiente aproximacin en normas especficas es motivo de preocupacin y estudio.

        p La coexistencia entre la sociolingstica y la dialectologa es an ms evidente en los trminos con los que algunos autores dan a conocer a la sociolingstica, a saber, dialectologa social, dialectologa urbana y sociodialectologa.
