          )
          .bloque-texto-g__texto.p-4
            p.mb-0 El inters por los dialectos y su estudio se inicia a finales del siglo XVIII. Para definir el trmino dialecto, es necesario tener claridad en otros conceptos que estn ntimamente relacionados entre s, a saber, lengua, estilo de lengua y nivel de lengua. Muchos acadmicos han intentado estudiar y clasificar dialectalmente el espaol de Amrica, acabando en esbozos o propuestas de una clasificacin panhispnica, mientras que otros han aportado algunas precisiones tericas para tal clasificacin. Uno de ellos es la biparticin dialectal entre el espaol americano con el peninsular de Espaa.

      .col-lg-10(data-aos="fade-in")
