    p(data-aos="fade-in") Las causas del desarrollo de los diferentes dialectos o variedades de las lenguas son numerosas, pero bsicamente son de dos tipos, a saber:

    .bg_darkBlue
      h3.text-white.text-center.mb-0 Causas del desarrollo de dialectos

    .bg_blue_pink.mb-5(data-aos="zoom-in")
      .row.justify-content-center
        .col-lg-4.mb-4.mb-lg-0
          h3 Factores externos a la lengua (histricos, geogrficos, sociales, etc.)
          p.mb-0 Pueden darse debido a la migracin de grupos de alguna regin especfica. Por ejemplo, se puede decir que a Argentina llegaron ms italianos, a Per ms castellanos y a Venezuela ms canarios. Estos grupos incidieron en el tiempo y la geografa, adems de la distancia social mezclado con los factores de la edad, el sexo y/o la clase social. Este ltimo, llamado tambin sociolecto, por desarrollarse en un mismo lugar geogrfico y contexto social forma un smbolo de identidad grupal fuerte.
        .col-lg-4.col-10.mb-4.mb-lg-0.V-align
          figure
            img(src="@/assets/curso/temas/t2/t2_11.svg", alt="alt")
        .col-lg-4
          h3 Factores internos, propios de la lengua y de su estructura
          p.mb-0 Al transcurrir el tiempo, las lenguas cambian de forma natural. As, los hablantes de una misma lengua pueden ofrecer diversas posibilidades de expresarse, por ello, cuando algn grupo se inclina por una de estas, nace una diferencia dialectal. Estos factores incluyen procesos de regularizacin y generalizacin de patrones lingsticos.


