    p(data-aos="fade-in") La divisin adoptada del Atlas de Colombia del Instituto Geogrfico Agustn Codazzi (1977), refiere desde el punto de vista de la etnografa y la sociologa (comunidad criolla colombiana) y la divide en ocho grupos: costeo, caucano, antioqueo, santandereano, cundiboyacense, llanero, tolimense y nariense.
