          .bloque-texto-g__texto.p-4
            p.mb-0 Lo caracterstico en la mayora de las personas es la constante mezcla del lenguaje culto con el lenguaje vulgar u ordinario en lo que corresponde a vocabulario, morfologa y fontica. La variedad situacional o diafsica ms utilizada de la lengua es el llamado registro coloquial, que se caracteriza por ser espontneo, relajado y expresivo; en un contexto informal, familiar y distendido. Se invita a revisar el siguiente ejemplo:

