              p.mb-0 Es una forma de tratamiento pronominal que utiliza el vos del singular en lugar del t, y que ha sido muy escrito, pero poco estudiado en Colombia por ser un tema de carcter regional y no generalizado como en Argentina o Costa Rica.
        .col-11.mb-4
