            .crd--avatarV--body
              h3.text-center De dnde surgi?
              p.mb-0  Lo que se conoce de la historia de este uso coloquial es que antes de la conquista, los espaoles usaban el voseo en un trato respetuoso en la alta sociedad, para despus traerlo a Amrica.
        .col-11.mb-4
