                li.mb-2
                  i.fa-li.fas.fa-chevron-right
                  | Elipsis y frases inacabadas.
                li.mb-2
                  i.fa-li.fas.fa-chevron-right
                  | Oraciones cortas y sencillas.
                li.mb-2
                  i.fa-li.fas.fa-chevron-right
                  | Uso de onomatopeyas.
                li.mb-2
