    .cajon.color-rosa.p-4.mb-5(data-aos="zoom-in")
      p.mb-0 El uso diario de la lengua en la cotidianidad se escapa de la terminologa acadmica, industrial o de alguna disciplina especfica. Es conocido tambin como lenguaje tcnico o tecnicismos y se refiere a todo el vocabulario o expresiones que pertenecen a un rea o campo profesional especfico. Generalmente, el intrprete o traductor tiene entrenamiento en algn campo y no en todas las reas.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-in")
        p No solo se trata de conocer la terminologa, sino tambin de reflejar el tono de manera objetiva, por lo que el traductor e intrprete debe entender y estar familiarizado con el tema. El grado de comprensin y emisin del mensaje depender del nivel de informacin que posea el intrprete. Es ms comn encontrar traductores tcnicos que intrpretes de lengua de seas colombiana especializados en un campo profesional.

        p.mb-0 Los tecnicismos son monosmicos, por lo que solo los especialistas pueden distinguir con precisin trminos propios de su ciencia. En ocasiones, un trmino puede tener la apariencia de una palabra del lxico coloquial, pero cuando se lee en un contexto tcnico su significado es unvoco. Por lo tanto, el vocabulario tcnico no puede ser empleado en el lenguaje coloquial. Lo nico que estos pueden tener en comn es su forma gramatical.
      .col-lg-4.col-10(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t2/t2_19.png", alt="alt")

    p.mb-5(data-aos="fade-in")  Por ejemplo, el trmino abandono es expuesto a los tecnicismos y a una descripcin propia en diferentes diccionarios, as:

    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="zoom-in")
        ImagenInfografica.color-acento-contenido
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/t2/t2_20.svg', alt='')


          .tarjeta.color-acento-contenido.p-3(x="24%" y="7%" numero="+")
            .h5.mb-2  Diccionario de la Real Academia Espaola - RAE
            p #[strong Abandono:] dejar solo algo o alguien alejndose de ello o dejando de cuidarlo.

          .tarjeta.color-acento-contenido.p-3(x="75%" y="7%" numero="+")
            .h5.mb-2 Diccionario virtual de biologa - BioDic
            p #[strong Abandono:] se aplica al fracaso de los padres o tutores para proporcionar las necesidades bsicas a un nio por una privacin emocional o fsica que interfiere con el crecimiento y desarrollo normal o que pone al nio en peligro.

          .tarjeta.color-acento-contenido.p-3(x="24%" y="95%" numero="+")
            .h5.mb-2 Diccionario de ciencias jurdicas, polticas y sociales, edicin electrnica de Manuel Osorio.
            p #[strong Abandono:] concepto ms amplio que los de renuncia y dimisin, que en ningn caso pueden referirse a obligaciones o derechos que por su naturaleza o por la ley tienen carcter irrenunciable.

          .tarjeta.color-acento-contenido.p-3(x="75%" y="95%" numero="+")
            .h5.mb-2 Diccionario etimolgico castellano en lnea
            p #[strong Abandono:] viene del verbo abandonar y este del francs abandonner, y este de la expresin #[i laisser  bandon] (dejar en poder de alguien).

    .row.justify-content-center.mb-5(data-aos="zoom-in")
      .col-lg-10
        .cajon.color-celeste.p-4
          .row.align-items-center
            .col.col-md-auto.mb-4.mb-md-0
              figure
                img(src="@/assets/curso/temas/t2/t2_21.png", alt="alt").img85.m-auto
            .col-md
              p.mb-0 A medida que el ser humano hace nuevos descubrimientos, hay que darles nombre, inventar vocablos nuevos, hay que crear neologismos; lo que tambin resulta extrao a la lengua y hace que la terminologa tcnica vaya en aumento. Mucha de la documentacin cientfica y tcnica est escrita en idiomas extranjeros, lo que requiere encontrar equivalencias entre idiomas para determinar el trmino correspondiente, de otro modo, hay que recurrir a los #[strong neologismos].

    separador

    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.2 Factores gramaticales, pragmticos y semnticos de la lengua fuente y meta (espaol - LSC y LSC - espaol)

    p.mb-5(data-aos="fade-in") Qu es la lengua fuente y la lengua meta? A continuacin, sus definiciones:

    .row.justify-content-center.mb-5
      .col-lg-6.mb-4.mb-lg-0(data-aos="flip-right")
        .crd-avatar-Horizontal.hg-100
          .row
            .col-auto
              figure
                img(src="@/assets/curso/temas/t2/t2_22.svg", alt="alt").img85
            .col
              h3 Lengua fuente
              p.mb-0 Hace referencia a la lengua de partida, el objeto de la traduccin o interpretacin. Tambin llamada de origen o de salida.
      .col-lg-6(data-aos="flip-right")
        .crd-avatar-Horizontal.hg-100
          .row
            .col-auto
              figure
                img(src="@/assets/curso/temas/t2/t2_23.svg", alt="alt").img85
            .col
              h3 Lengua meta
              p.mb-0 Es aquella como resultado de la traduccin o interpretacin, llamada tambin lengua traducida (traduccin) o interpretada (interpretacin).

    p.mb-5(data-aos="fade-in") Para este componente se har referencia a las dos lenguas que corresponden a este programa: la Lengua de Seas Colombiana (LSC) y el espaol (escrito o hablado), siendo cualquiera de las dos la lengua fuente o la lengua meta. Por ello, se analizarn los factores gramaticales, pragmticos y semnticos de las dos lenguas.

    .row.justify-content-start.mb-4
      .col-auto.bg9-1.p-0(data-aos="flip-down")
        .list.mb-1
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/v2.svg', alt='')
          .list__txt
            h4.mb-0 Qu es la gramtica en las lenguas orales?

    p.mb-5(data-aos="fade-in") El trmino gramtica viene del latn #[i grammatca], y este del griego #[i grammatiktkhne], donde #[i tkhne] significa arte o tcnica y #[i grammatik] viene de #[i grmma], que significa letra. Para los antiguos, este arte de las letras abarcaba todos los aspectos del discurso: ortografa, sintaxis, interpretacin de los textos, entre otros.

    .row.justify-content-center.mb-5(data-aos="zoom-in")
      .col-lg-10.mb-4
        .crd-cyan02.p-4
          .row.justify-content-center.align-items-center
            .col-lg-3.col-10.mb-4.mb-lg-0
              figure
                img(src="@/assets/curso/temas/t2/t2_24.svg", alt="alt")
            .col-lg-9
              p.mb-0 La gramtica es el estudio de las reglas y principios que rigen el uso de las lenguas y la organizacin o combinacin de las palabras dentro de las oraciones. La gramtica es una parte de la lingstica o el estudio general del lenguaje.

      .col-lg-10(data-aos="fade-in")
        p.mb-0 Para algunos expertos, la gramtica comprende solo la morfologa y la sintaxis, mientras que para otros tambin los sonidos y los fonemas; a continuacin, se invita a revisar a qu hace referencia cada uno de estos trminos:

    .bg-slyder.p-5.mb-5(data-aos="zoom-in")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-center
          .col-lg-7.mb-4.mb-lg-0
            h3 Morfologa
            p.mb-0 Se encarga de la estructura de las palabras, de sus componentes internos, definiendo y clasificando sus unidades: morfologa flexiva (las variantes de las palabras), la morfologa derivativa (la formacin de nuevas palabras, afijos) y los neologismos (composicin).
          .col-lg-5.col-8
            figure
              img(src='@/assets/curso/temas/t2/t2_25.svg', alt='')

        .row.justify-content-center.align-items-center
          .col-lg-7.mb-4.mb-lg-0
            h3 Sintaxis
            p.mb-0 Estudia las reglas y principios de los sintagmas (palabras o secuencia de palabras), as como sus funciones, clasificacin, combinacin y orden dentro de las oraciones.
          .col-lg-5.col-8
            figure
              img(src='@/assets/curso/temas/t2/t2_26.svg', alt='')

        .row.justify-content-center.align-items-center
          .col-lg-7.mb-4.mb-lg-0
            h3 Fonologa
            p.mb-4 Se ocupa de estudiar los fonemas o sonidos voclicos y consonnticos que forman una lengua. Interpreta la manera como los sonidos surgen a nivel abstracto o mental. Se escriben entre diagonales para representar el sonido grficamente, por ejemplo:
            p #[i.bold /s/ + /o/ + /l/ = sol]
          .col-lg-5.col-8
            figure
              img(src='@/assets/curso/temas/t2/t2_27.svg', alt='')

    .row.justify-content-start.mb-4
      .col-auto.bg9-1.p-0(data-aos="flip-down")
        .list.mb-1
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/v2.svg', alt='')
          .list__txt
            h4.mb-0 Gramtica en LSC

    p.mb-5(data-aos="fade-in") Al igual que en las lenguas orales y segn las investigaciones de William Stokoe, las lenguas de seas tambin se estudian, tienen reglas y principios que rigen su uso, organizacin y combinacin de sus diferentes partes dentro del discurso. No son simples cdigos rudimentarios ni lingsticos.

    .row.justify-content-center.mb-5(data-aos="zoom-in")
      .col-lg-10
        .bloque-texto-c.color-rosa-claro.p-4
          i.fas.fa-quote-right
          h4 Las lenguas de seas son lenguas naturales. Poseen la organizacin gramatical establecida por la lingstica para cualquier lengua, como son a) la doble articulacin, b) la arbitrariedad del signo y c) la productividad
          span.me-5.float-end - (Stokoe, 1981)

