        p Las seas pueden alterar su forma sistemticamente, aunque existen algunas que no lo hacen, son las que sirven como conectores lgicos del discurso como los son: entonces, despus, aparte, pero, y los decticos interrogativos como por ejemplo: qu, dnde, cundo, quin, qu pasa. Adems, algunas seas no pueden ser segmentadas en partes ms pequeas, debido a que ellas mismas son las unidades mnimas.

        p.mb-0 En una conversacin o discurso en LSC, una sea puede asumir algn rasgo de la sea que la antecede o de la sea que le sucede, lo que Oviedo (2001) llama asimilacin de la localizacin. Esta variacin no es significativa, es decir, no aade ni quita significado al que ya tiene la sea. Tambin pueden notarse cambios en las locaciones de inicio o fin, cambios de configuracin manual, cambios de variacin en el nmero de las seas por repeticin en el espacio o por morfemas de nmero en las configuraciones.

