    p.mb-5(data-aos="fade-in") La palabra sintaxis viene del griego #[i sin] que significa con y #[i taxis] que es orden; se invita a dar clic en el botn para profundizar en el trmino:

    .row.justify-content-center.mb-5(data-aos="zoom-in")
