            .col-md
              p.mb-0 #[strong Sintaxis:] Es la parte de la gramtica que pone orden a las palabras y forma oraciones. En el caso de las lenguas de seas se puede hablar de una sintaxis o modo en el que estn ordenadas las seas con su significado propio y sus partes, para transmitir mensajes complejos.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-4.col-10.mb-4.mb-lg-0(data-aos="zoom-in")
        figure
          img(src="@/assets/curso/temas/t2/t2_37.svg", alt="alt")
