        p Los rasgos articulatorios corporales (cabeza, manos, cuerpo) y rasgos no manuales, se combinan entre s de acuerdo con ciertas reglas para formar oraciones. As, las unidades sintcticas ms elementales son, en ocasiones, las mismas seas debido a su complejidad, con partculas internas que tienen significado en s. Sin embargo, los rasgos como la locacin y la orientacin tambin se presentan como unidades autnomas que pueden cambiar de significado las seas. Otro rasgo de la LSC es que una sea puede ser la unidad sintctica o varias seas juntas que la conforman.

